}
@font-face {
  font-family: "TT Lakes";
  src: url("../fonts/TTLakes-Bold.eot");
  src: local("TT Lakes Bold"), local("TTLakes-Bold"), url("../fonts/TTLakes-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/TTLakes-Bold.woff") format("woff"), url("../fonts/TTLakes-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
body {
  margin: 0;
  position: relative;
  font-size: 16px;
  line-height: 1.5;
  font-family: "TT Lakes", sans-serif;
  letter-spacing: -0.2px;
  background-color: #F8F8F8;
  color: #1B1C21;
}

*,
::before,
::after {
  box-sizing: inherit;
}

:root {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.btn-around {
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: #FF3E79;
  border: 0;
  border-radius: 50%;
  cursor: pointer;
  filter: opacity(1);
  transition-duration: 0.3s;
  transition-property: filter;
  appearance: none;
}
.btn-around:hover {
  filter: opacity(0.6);
}
.btn-around img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.btn-around:hover img {
  transform: translate(-50%, -50%) scale(1.1);
}

.title {
  position: relative;
  margin: 0;
  padding-left: 12px;
  font-weight: bold;
  font-size: 28px;
  line-height: 40px;
  letter-spacing: -0.6px;
}
.title::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 4px;
  height: 32px;
  background: #41f6d7;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  transform: translateY(-50%);
  height: 40px;
}

@media (min-width: 768px) {
  .title {
    padding-left: 20px;
  }
}
@media (min-width: 1440px) {
  .title {
    padding-left: 30px;
  }
}
.section-title {
  margin: 0;
  padding: 16px;
  font: inherit;
  color: #7E7E82;
  text-transform: uppercase;
  background: linear-gradient(90deg, #f8f8f8 1.39%, #ffffff 100%);
}

.showmore {
  display: none;
  padding: 0;
  font: inherit;
  font-weight: 500;
  color: inherit;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  appearance: none;
}
.showmore::before {
  content: "";
  display: inline-block;
  width: 7px;
  height: 10px;
  margin-right: 16px;
  margin-left: 5px;
  background-image: url(../img/icons/readmore.svg);
  background-size: contain;
}

@media (min-width: 768px) {
  .showmore {
    display: block;
  }
  .showmore--show::before {
    transform: rotate(180deg);
  }
}
.modal {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 40;
  width: 100%;
  height: 100%;
  padding: 24px 16px 32px;
  overflow: auto;
  background-color: #F8F8F8;
  transform: translateX(110%);
  transition: 0.6s all;
}
.modal--show {
  transform: translateX(0%);
  box-shadow: 16px 0 52px rgba(14, 24, 80, 0.2), -2px 0 4px rgba(69, 79, 126, 0.02);
}
.modal__close {
  margin-bottom: 40px;
}
.modal__title {
  margin-bottom: 24px;
  margin-left: -16px;
}
.modal__form {
  margin-bottom: 16px;
}
.modal__text {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82;
}
.modal__warning {
  font-weight: 500;
  color: #ff3e79;
}
.modal__button {
  margin-left: auto;
}
.modal__input {
  margin-bottom: 16px;
}

@media (min-width: 768px) {
  .modal {
    width: 320px;
  }
}
.input {
  width: 100%;
  height: 48px;
  min-height: 48px;
  padding: 12px 16px;
  font: inherit;
  background: #ffffff;
  border: 1px solid #eaeaea;
  border-radius: 8px;
}
.input::placeholder {
  color: #b5b6bc;
}
.input--textarea {
  height: 118px;
  resize: none;
}

.button {
  display: block;
  min-width: 120px;
  height: 32px;
  min-height: 32px;
  padding: 0 12px;
  padding-right: 20px;
  padding-left: 20px;
  font-weight: bold;
  font-size: 12px;
  line-height: 24px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: -0.15px;
  background-color: #FF3E79;
  border: 0;
  border-radius: 16px;
  cursor: pointer;
  appearance: none;
}
.button::after {
  content: "";
  display: inline-block;
  width: 8px;
  height: 10px;
  margin-bottom: 1px;
  margin-left: 13px;
  vertical-align: middle;
  background-image: url(../img/icons/dropdown.svg);
  background-repeat: no-repeat;
  background-size: contain;
}

.switch-lang__lang {
  margin-right: 7px;
  padding: 0;
  font-weight: 500;
  font-size: inherit;
  font-family: inherit;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  appearance: none;
}
.switch-lang__lang:last-of-type {
  margin-right: 0;
}
.switch-lang__lang--active, .switch-lang__lang:hover {
  color: #7E7E82;
}

.link-around {
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
  text-decoration: none;
  background-color: transparent;
  border: 2px solid #FF3E79;
  border-radius: 50%;
}
.link-around::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 8px;
  height: 12px;
  background-image: url(../img/icons/arrow-right.svg);
  background-repeat: no-repeat;
  background-size: contain;
  transform: translateX(-50%) translateY(-50%);
}

.contacts__email {
  margin-bottom: 12px;
  font-weight: 500;
  color: inherit;
  text-decoration: none;
}
.contacts__phone {
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  color: inherit;
  text-decoration: none;
  letter-spacing: -0.6px;
}

.header {
  min-height: 88px;
  padding: 19px 0;
  padding-left: 16px;
  padding-right: 16px;
  background-color: #ffffff;
  border-bottom: 1px solid #d9fff5;
}
.header__wrapper {
  display: flex;
  align-items: center;
}
.header__logo {
  margin-right: auto;
}
.header__help {
  display: none;
}
.header__btn-around {
  flex-shrink: 0;
  margin-right: 16px;
}
.header__btn-around:last-child {
  margin-right: 0;
}
.header__right {
  display: inline-flex;
}
div + .header__right::before {
  content: "";
  display: inline-block;
  width: 2px;
  height: 37px;
  margin: auto 16px;
  background-color: #eaeaea;
  border-radius: 1px;
}

@media (min-width: 360px) {
  .header {
    padding-left: 24px;
    padding-right: 24px;
  }
}
@media (min-width: 768px) {
  .header__btn-around ~ .header__logo::before {
    content: "";
    display: inline-block;
    width: 2px;
    height: 37px;
    margin: auto 16px;
    background-color: #eaeaea;
    border-radius: 1px;
    margin-left: 0;
  }
  .header__help {
    display: inline-flex;
  }
}
@media (min-width: 1440px) {
  .header {
    display: none;
  }
  .header__toggle {
    display: none;
  }
}
.nav {
  display: flex;
}
.nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav__descr {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
}

.logo {
  display: inline-flex;
  text-decoration: none;
}

.main {
  display: block;
  padding-top: 24px;
  padding-bottom: 40px;
}
.main > section {
  border-bottom: 1px solid #d9fff5;
}

@media (min-width: 768px) {
  .main {
    padding-bottom: 47px;
  }
}
@media (min-width: 1440px) {
  .main {
    padding-top: 42px;
    padding-bottom: 50px;
  }
}
.services__header {
  margin-bottom: 32px;
  padding-right: 10px;
}
.services__modal {
  display: none;
}
.services__wrapper-menu {
  height: 36px;
  margin-bottom: 32px;
  overflow: hidden;
}
.services__menu {
  display: flex;
  flex-wrap: nowrap;
  padding: 0 16px;
  overflow: auto;
}
.services__tab {
  flex-shrink: 0;
  margin-right: 20px;
}
.services__tab:last-child {
  margin-right: 0;
}
.services__info {
  margin-bottom: 30px;
}
.services__descr {
  margin-left: 16px;
  margin-right: 16px;
}
.services__text {
  margin-bottom: 10px;
  font-size: 14px;
  letter-spacing: 0.2px;
}
.services__text p {
  margin: 0;
  margin-top: 20px;
}
.services__text p:first-child {
  margin-top: 0;
}
.services__text-path1, .services__text-path2, .services__text-path3 {
  display: none;
}
.services__text-path1 {
  display: inline;
}
.services__img {
  margin: 0 auto;
}
.services__img img {
  display: block;
  width: 100%;
}

@media (min-width: 768px) {
  .services__info {
    display: flex;
    justify-content: space-between;
  }
  .services__text-path2 {
    display: inline;
  }
  .services__descr, .services__img {
    width: 49%;
  }
}
@media (min-width: 1440px) {
  .services__header {
    display: flex;
  }
  .services__title {
    margin-right: auto;
  }
  .services__modal {
    display: flex;
  }
  .services__caption {
    display: initial;
    margin-right: 8px;
    font-weight: 500;
    vertical-align: middle;
  }
  .services__item {
    display: inline-flex;
    align-items: center;
  }
  .services__item + .services__item {
    margin-left: 32px;
  }
  .services__wrapper-menu {
    height: auto;
    overflow: initial;
  }
  .services__menu {
    flex-wrap: wrap;
    overflow: initial;
  }
  .services__text-path3 {
    display: inline;
  }
}
.tab {
  display: inline-block;
  padding: 4px 10px 4px 16px;
  font-size: 16px;
  line-height: 24px;
  font-family: inherit;
  color: #7E7E82;
  letter-spacing: -0.2px;
  white-space: nowrap;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  appearance: none;
}
.tab--active {
  background-color: #ffffff;
  border: 2px solid #b8ffec;
  border-radius: 6px;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
  height: 100%;
  padding-top: 19px;
  padding-bottom: 32px;
  overflow: auto;
  background-color: #ffffff;
  transform: translateX(-100%);
  transition: 0.6s all;
  padding-left: 16px;
  padding-right: 16px;
}
.sidebar--show {
  transform: translateX(0%);
}
.sidebar__header {
  display: block;
  min-height: auto;
  margin-bottom: 25px;
  padding: 0;
  border-bottom: 0;
}
.sidebar__btn-around {
  margin-right: 16px;
}
.sidebar__btn-around:last-of-type {
  margin-right: 0;
}
