}
.sidebar__nav {
  margin-bottom: 50px;
}
.sidebar__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}
.sidebar__item {
  position: relative;
  margin-bottom: 32px;
  font-weight: 500;
}
.sidebar__item:last-child {
  margin-bottom: 0;
}
.sidebar__item--active {
  color: #7E7E82;
}
.sidebar__item--active::before {
  margin-left: -16px;
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 4px;
  height: 32px;
  background: #41f6d7;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  transform: translateY(-50%);
}
.sidebar__link {
  color: inherit;
  text-decoration: none;
}
.sidebar__help {
  display: flex;
  margin-bottom: 16px;
}
.sidebar__help-item {
  margin-right: 16px;
}
.sidebar__help-item:last-child {
  margin-right: 0;
}
.sidebar__footer {
  margin-top: auto;
}
.sidebar__contacts {
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
